 

 @media screen and (max-width:1280px){
	html,body{overflow-x:hidden;}
}

.container {   
  //max-width: 1280px;
  min-width: 320px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 20px;
  padding-right: 20px;
  margin-top: 113px;      
  
    
    @media screen and (min-width: 768px) {
      padding-left: 36px;
      padding-right: 36px; 
      margin-top: 165px;
      
    } 

    @media screen and (min-width: 1280px) {
      padding-left: 115px;
      padding-right: 115px; 
      margin-top: 153px;
      
    } 
  // @media screen and (min-width: 768px) {
  //   width: 768px;
  // } 
  // @media screen and (min-width: 1280px) {
  //   width: 1280px;
  // }
}
.footer__text__title--one {
  font-family: $titleFont;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: calc(21 / 18);
  text-transform: uppercase;
  color: $LabelColor;
  padding: 0;
  margin: 0;
  margin-bottom: 17px;
  

  @media screen and (min-width: 768px)  {
    font-family: $titleFont;     
    font-size: 26px;
    line-height: calc(30 / 26);
    letter-spacing: 0.04em; 
        
  }

  @media screen and (min-width: 1280px) {
    font-size: 30px;
    line-height: calc(34 / 30);
    letter-spacing: 0.04em; 
    max-width: 100%;  
}
}

.footer__text__title--two {
 font-family: $titleFont;   
  font-weight: normal;
  font-size: 34px;
  line-height: calc(39 / 34);
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: $mainDarkColor;
  padding: 0;
  margin: 0;
   
  margin-bottom: 43px;

  @media screen and (min-width: 768px) {
       
     font-size: 48px;
     line-height: calc(55 / 48);
     letter-spacing: 0.06em;
     text-transform: uppercase;     
   }

  @media screen and (min-width: 1280px) {
     font-size: 58px;
     line-height: calc(66 / 58);
     letter-spacing: 0.06em;     
 }
}

.footer__text {
  width: 234px;     
  margin-bottom: 43px;
    @media screen and (min-width: 768px), (min-width: 1280px) {
       width: 50%;
       
     
    }

