  margin-left: 28.24px;
}

 

.footer__tel {
  font-family: $mainFont;
  color: $mainTextColor;
  text-decoration: none;
  font-weight: bold;
  font-size: 12px;
  line-height: 186%;
 
    @media screen and (min-width: 768px), (min-width: 1280px) {
     font-size: 14px;
    } 
  
