.footer__contact {
   
  color: $mainDarkColor;
}

.footer__text__tel {
  text-align: right;
  margin-bottom: 43px;
}

   
.copyright {
  font-family: $mainFont;
  font-style: normal;
  font-weight: bold;
  max-width: 150px;
  margin: 0 auto;
  font-size: 14px;
  line-height: 152%;
  text-align: center;
  color: $secondaryTextColor;
  margin-bottom: 25px;

  @media screen and (min-width: 768px), (min-width: 1280px) {
     max-width: 100%;
     line-height: 186%;
     margin-bottom: 18px;
   } 
}








.total__link__social {
  display: flex;  
  background-position: center;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
   width: 32px;
  height: 32px;
  background-color: $mainColor;

  transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
  &:hover,
  &:focus {
      background-color:$accentDarkBtnColor;
    }

    
 } 

 .total__icon {
  width: 20px;
  height: 20px;    
  fill: $mainLightColor;   
}

